.spinner {
    display: inline-block;
    width: 100px;
    height: 100px;
  }
  .spinner:after {
    content: ' ';
    display: block;
    width: 64px;
    height: 64px;
    /* margin: 8px; */
    border-radius: 100%;
    border: 6px solid #F9BA15;
    border-color: #F9BA15 transparent #F9BA15 transparent;
    animation: spinner 1.2s linear infinite;
  }

  @keyframes spinner {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  